<template lang="html">
  <div class="signUp">
    <h1>Concluir Cadastro</h1>
    <div class="page">
      <VForm :user="user" :handleSubmit="handleSubmit"></VForm>
    </div>
  </div>

</template>

<script>
import VForm from './Form.vue'
import baseService from '../services/baseService'

export default {
  name: 'signUp',
  components: {
    VForm
  },
  data () {
    return {
      user: {
        name: '',
        email: '',
        password: '',
        phone: '',
        birthday: '',
        type: 2,
        organizationid: 7,
        Identifyer: '',
        id: ''
      },
      rpwd: ''
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    getData: function () {
      this.user.id = this.$route.params.id
      baseService.get(`/user/${this.user.id}`).then(r => {
        this.user = r.data
      })
    },
    handleSubmit: function (user) {
      baseService.put(`/user/${this.user.id}`, user)
    }
  }
}
</script>

<style lang="css">

.signUp {
  display: flex;
  flex-direction: column;
  height: 77vh;
  margin: 10px 40px;
  overflow-y: scroll;
  overflow-x: hidden;
  align-content: center;
}

h1 {
  text-align: center;
}

.page {
  display: flex;
  justify-content: center;
}

.signUp .vform {
  width: 100%;
  margin-left: 300px;
  margin-right: 300px;
}



</style>



